import { Component, OnInit } from '@angular/core';
import { KisanPortalService } from '../kisan-portal.service';

@Component({
  selector: 'app-adduser',
  templateUrl: './adduser.component.html',
  styleUrls: ['./adduser.component.css']
})
export class AdduserComponent implements OnInit {
  errorMsg: any = 'Unable to Save the User Details, Please Try again.';
  successMsg: any = 'User is Saved Successfully.';
  errorFlag: boolean = false;
  successflag: boolean = false;
  userDetails = {
    userId: '',
    password: '',
    confirmPassword: '',
    userName: '',
    userRole: '',
    location: ''
  }
  constructor(private kisanPortalService: KisanPortalService) { }

  ngOnInit() {
  }
  saveUser(){
    console.log(this.userDetails);
    if(this.userDetails.password == this.userDetails.confirmPassword){
    this.kisanPortalService.saveUser(this.userDetails).subscribe(
      (data) => {
        console.log(data);
        this.errorFlag = false;
        this.successflag = false;
        this.userDetails = {
          userId: '',
          password: '',
          confirmPassword: '',
          userName: '',
          userRole: '',
          location: ''
        }
        this.successflag = true;
        this.successMsg = "User is Saved Successfully.";
      },
      Error => {
        this.successflag = false;
        this.errorFlag = true;
        this.errorMsg = "Unable to Save the User Details, Please Try again.";
      })
    }else{
      this.errorFlag = true;
      this.errorMsg = "Password not matched.";
    }
  }
}
