import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { AddTransactionComponent } from './add-transaction/add-transaction.component';
import { KisanDashboardComponent } from './kisan-dashboard/kisan-dashboard.component';
import { AddfarmerComponent } from './addfarmer/addfarmer.component';
import { AdduserComponent } from './adduser/adduser.component';
import { DashboardhomeComponent } from './dashboardhome/dashboardhome.component';

const routes: Routes = [
  {
    path: 'kisanDashboard', component: KisanDashboardComponent,
    children: [
      {
        path: '',
        component: DashboardhomeComponent
      },
      {
        path: 'addProduct',
        component: AddTransactionComponent
      },
      {
        path: 'addFarmer',
        component: AddfarmerComponent
      },
      {
        path: 'addUser',
        component: AdduserComponent
      }
    ]
  }
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes)
  ],
  exports: [RouterModule]
})

export class AppRoutingModule { }
