import { Injectable } from '@angular/core';
import { AppComponent } from './app.component';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
//import 'rxjs/add/operator/map';
import { ItemsList } from './add-transaction/ItemsList';
@Injectable({
  providedIn: 'root'
})
export class KisanPortalService {
  loginsuccess: boolean = true;

  getItemsListUrl = 'http://localhost:8080/getItemsList';
  saveTransactionUrl = 'http://localhost:8080/SaveTransaction';
  deleteTransactionUrl = 'http://localhost:8080/deleteTransaction';
  saveFarmerUrl = 'http://localhost:8080/SaveFarmer';
  saveUserUrl = 'http://localhost:8080/Saveuser';
  getTransactionUrl = 'http://localhost:8080/getTransaction'
  constructor(private http: Http) { }
  setLoginsuccess(loginsuccess) {
    this.loginsuccess = loginsuccess;
  }
  getLoginsuccess() {
    return this.loginsuccess;
  }
  getItemsList(): Observable<any> {
    return this.http.get(this.getItemsListUrl).pipe(map(data => data.json()));
  }

  getTransaction(barCode):Observable<any>{
    this.getTransactionUrl = this.getTransactionUrl+"/"+barCode;
    return this.http.get(this.getTransactionUrl,barCode)
  }

  saveTransactionDetails(transData): Observable<any> {
    return this.http.post(this.saveTransactionUrl, transData);
  }
  deleteTransaction(TransId){
    return this.http.put(this.deleteTransactionUrl, TransId);
  }
  saveFarmer(farmerData): Observable<any>{
    return this.http.post(this.saveFarmerUrl, farmerData);
  }
  saveUser(userData): Observable<any>{
    return this.http.post(this.saveUserUrl, userData);
  }
}
