import { Component, OnInit, Inject } from '@angular/core';
import { KisanPortalService } from '../kisan-portal.service';
import { SESSION_STORAGE, WebStorageService } from 'angular-webstorage-service';

@Component({
  selector: 'app-addtransaction',
  templateUrl: './add-transaction.component.html',
  styleUrls: ['./add-transaction.component.css']
})
export class AddTransactionComponent implements OnInit {
  errorMsg: any = 'Unable to Save the Transaction, Please Try again.';
  successMsg: any = 'Your Transaction is Saved Successfully.';
  errorFlag: boolean = false;
  successflag: boolean = false;
  userRole: String = '';
  crop_type: any = '';
  crop_id: any = '';
  item_types = [];
  cropList: any = [];
  transactionData = {
    transId: '',
    farmer_id: '',
    item_id: '',
    source_location: '',
    create_date: '',
    qantity: '',
    grade: '',
    status: '',
    storage_loc: '',
    storage_date: '',
    price: '',
    selling_loc: '',
    selling_date: ''
  };
  editButton: boolean = false;
  updateTransButton : boolean = false;
  cropError: boolean = false;
  cropErrorMsg: string = '';
  crops: any = [];
  constructor(private kisanPortalService: KisanPortalService, @Inject(SESSION_STORAGE) private sessionStorage: WebStorageService) { }
  ngOnInit() {
    debugger;
    this.userRole = JSON.parse(sessionStorage.getItem("userRole").toString());
    //if (this.userRole != 'admin') {
      this.kisanPortalService.getItemsList().subscribe((data) => {
        data.forEach(element => {
          console.log('element.item_desc : ' + element.item_desc);
          this.crops.push(element);
        });
        console.log(this.crops);
        this.item_types = Array.from(new Set(this.crops.map((IteminArray) => IteminArray.item_type)));
        console.log("uniq  :  " + this.item_types);
      });
    //}
  }
  fetchTransactionData() {
    debugger;
    if (this.userRole == 'admin') {
      this.kisanPortalService.getTransaction(this.transactionData.transId).subscribe((data) => {
        this.transactionData = data;
        this.editButton = true;
        console.log(this.transactionData);
      });
    } else {
      return;
    }
  }
  cropTypeSelected() {
    console.log(this.crop_type);
    this.transactionData.item_id = '';
    this.cropList = [];
    if (this.crop_type != '' && this.crop_type) {
      this.cropError = false;
      console.log(this.crop_type);
      this.crops.forEach(element => {
        console.log('ele' + element.item_name);
        if (element.item_type === this.crop_type) {
          this.cropList.push(element);
        }
      });
      console.log(this.cropList);
    }
  }
  cropSelected() {
    //debugger;
    if (this.crop_type == '' || !this.crop_type) {
      this.cropError = true;
      this.cropErrorMsg = 'Please Select Crop Type';
      console.log(this.crop_id);
      return false;
    } else {
      this.cropError = false;
    }
    console.log(this.transactionData.item_id);
  }
  saveTransactionDetails() {
    console.log(this.transactionData);
    this.kisanPortalService.saveTransactionDetails(this.transactionData).subscribe(
      (data) => {
        this.errorFlag = false;
        this.successflag = false;
        this.clearTransactionDetails();
        console.log(data);
        this.crop_type = '';
        this.successflag = true;
        this.successMsg = "Your Transaction is Saved Successfully.";
      },
      Error => {
        this.errorFlag = true;
        this.errorMsg = "Unable to Save the Transaction, Please Try again.";
      })
  }
  editTransaction() {
    this.editButton = false;
    this.updateTransButton = true;
  }
  deleteTransaction(){
    console.log("Transaction to be deleted "+this.transactionData.transId);
    this.kisanPortalService.deleteTransaction(this.transactionData.transId).subscribe(
      (data) => {
        this.errorFlag = false;
        this.successflag = false;
        this.clearTransactionDetails();
        console.log(data);
        this.crop_type = '';
        this.successflag = true;
        this.successMsg = "Your Transaction Deleted Successfully.";
      },
      Error => {
        this.errorFlag = true;
        this.errorMsg = "Unable to Delete the Transaction, Please Try again.";
      })
  }
  clearTransactionDetails() {
    this.transactionData = {
      transId: '',
      farmer_id: '',
      item_id: '',
      source_location: '',
      create_date: '',
      qantity: '',
      grade: '',
      status: '',
      storage_loc: '',
      storage_date: '',
      price: '',
      selling_loc: '',
      selling_date: ''
    };
  }
}