import { Component, OnInit } from '@angular/core';
import { KisanPortalService } from '../kisan-portal.service';

@Component({
  selector: 'app-addfarmer',
  templateUrl: './addfarmer.component.html',
  styleUrls: ['./addfarmer.component.css']
})
export class AddfarmerComponent implements OnInit {
  errorMsg: any = 'Unable to Save the Farmer Details, Please Try again.';
  successMsg: any = 'Farmer is Saved Successfully.';
  errorFlag: boolean = false;
  successflag: boolean = false;
  farmerDetails = {
    farmerId: '',
    farmerName: '',
    village: '',
    age: '',
    mandal: '',
    gender: '',
    surveyNum: '',
    noOfAcres: '',
    ownLand: '',
    aadharNum: '',
    pan: '',
    accountNum: '',
    bankName: '',
    ifscCode: '',
    cultivableProds: ''
  }
  constructor(private kisanPortalService: KisanPortalService) { }

  ngOnInit() {
  }
  saveFarmer() {
    debugger;
    console.log("Former Details  : "+this.farmerDetails);
    this.kisanPortalService.saveFarmer(this.farmerDetails).subscribe(
      (data) => {
        console.log(data);
        this.errorFlag = false;
        this.successflag = false;
        this.successflag = true;
        this.clearFarmerData();
        this.successMsg = "Farmer is Saved Successfully.";
      },
      Error => {
        this.successflag = false;
        this.errorFlag = true;
        this.errorMsg = "Unable to Save the Farmer Details, Please Try again.";
      });
  }
  clearFarmerData() {
    this.farmerDetails = {
      farmerId: '',
      farmerName: '',
      village: '',
      age: '',
      mandal: '',
      gender: '',
      surveyNum: '',
      noOfAcres: '',
      ownLand: '',
      aadharNum: '',
      pan: '',
      accountNum: '',
      bankName: '',
      ifscCode: '',
      cultivableProds: ''
    }
  }
}
